<template>
  <div class="evaluation">
    <div>
      <div class="abstract bottom-line" v-if="abstract.length > 0">
        <div style="margin-top: 0.24rem; margin-bottom: 0.30rem;color: #000000;">介绍</div>
        <div class="content_style">{{abstract}}</div>
      </div>
      <div style="color: #000000;font-weight: 400;height: 24px;line-height: 24px;margin-top: 0.3rem;">评价 22</div>
      <div class="evaluation_score bottom-line">
        <div class="earn_score">
          <label style="font-size: 1.21rem;color: #000000;">4.2</label>
          <div class="score_star">
            <div class="star_row">
              <img v-for="n in commtens.kuaidi" src="../assets/star.png">
              <div class="score_progress"></div>
            </div>
            <div class="star_row">
              <img v-for="n in commtens.manyidu" src="../assets/star.png">
              <div class="score_progress"></div>
            </div>
            <div class="star_row">
              <img v-for="n in commtens.taidu" src="../assets/star.png">
              <div class="score_progress"></div>
            </div>
            <div class="star_row">
              <img v-for="n in commtens.shangpin" src="../assets/star.png">
              <div class="score_progress"></div>
            </div>
            <div class="star_row">
              <img v-for="n in commtens.kuaidiyuan" src="../assets/star.png">
              <div class="score_progress"></div>
            </div>
          </div>
        </div>
        <div class="score_info">
          <label>满分5分</label>
          <label>22个评分</label>
        </div>
      </div>
      <div class="my_evaluation bottom-line">
        <div style="display: flex;justify-content: space-between;">
          <label style="margin-top: 0.42rem;color: #000000;">我的评价</label>
          <div class="star_row" style="margin-top: 0.2rem;">
            <img v-for="n in commtens.taidu" src="../assets/star.png">
          </div>
        </div>
        <div style="margin-left: 0.91rem;margin-top: 0.18rem;">2018-02-06</div>
        <div class="content_style" style="margin-left: 0.91rem;margin-top: 0.20rem;">最近的游戏都很让人满足，期望日后更好的作品出现，阴阳师做的是我最新欢的。希望有更多类似的高品质游戏作品的发行！加油！加油！期待更多更好的作品！
        </div>
      </div>
      <div class="other_player_evaluation">
         <div style="margin-top: 0.24rem;color: #000000;">玩家评价</div>
      </div>
    </div>
  </div>
</template>

<script >
  export default {
    props: {
      data: {
        type: Array,
        default: []
      },
      abstract: {
        type: String,
        default: ''
      }
    },
    data() {
      return {
        commtens: {
          kuaidi: 5,
          manyidu: 4,
          taidu: 3,
          shangpin: 2,
          kuaidiyuan: 1
        }
      }
    },
    methods: {
      resolveQuestionFollowers (item) {
        return `${item.answers} 赞同 · ${item.followers} 评论`
      }
    }
  }
</script>


<style scoped lang="stylus">
  @import "~common/stylus/variable"

  .evaluation
    box-sizing: border-box
    margin-left: 0.40rem
    margin-right: 0.36rem
    color: $content-color
    font-size: 0.25rem
    .abstract
      padding-bottom: 0.36rem
    .evaluation_score
      display: flex
      flex-direction: column
      padding-bottom: 0.40rem
      .earn_score
        display: flex
        justify-content: space-between
      .score_info
        display: flex
        justify-content: space-between
        margin-top: 0.21rem
        margin-left: 0.4rem
    .my_evaluation
      padding-bottom: 0.40rem
    // .other_player_evaluation
  .star_row
    display: flex
    justify-content: flex-end
    align-items: center
    .score_progress
      width: 2.88rem
      height: 0.10rem
      margin-left: 0.14rem
      background: gray
    img
      width: 0.24rem
      height: 0.24rem
  
</style>


