<template>
  <div class="cube-page tab-composite-view" >
    <div class="wrapper">
      <cube-tab-bar v-model="selectedLabel"
                    :use-transition="disabled"
                    ref="tabBar"
                    :data="tabLabels">
        <cube-tab v-for="(item, index) in tabLabels" :label="item.label" :key="item.label">
          <div style="display: flex;flex-direction: column;align-items: center;">
            <label>{{item.label}}</label>
            <div style="background-color: #ffba15;height: 2px;width: 30px;margin-top: 0.1rem" v-show="index==current"></div>
          </div>
      </cube-tab>
      </cube-tab-bar>
      <div class="spec"></div>
      <div class="tab-slide-container">
        <cube-slide
          ref="slide"
          :loop="loop"
          :initial-index="initialIndex"
          :auto-play="autoPlay"
          :show-dots="showDots"
          :options="slideOptions"
          @scroll="scroll"
          @change="changePage"
        >

          <cube-slide-item>
            <cube-scroll :data="data" :options="scrollOptions">
              <slot name="left">默认左侧页</slot>
            </cube-scroll>
          </cube-slide-item>

          <cube-slide-item>
            <slot name="center">默认中间页</slot>
          </cube-slide-item>

          <cube-slide-item>
            <slot name="right">默认右侧页</slot>
          </cube-slide-item>

<!-- 
          <cube-slide-item>
            <cube-scroll :data="data" :options="scrollOptions">
              <slot name="left">默认左侧页</slot>
            </cube-scroll>
          </cube-slide-item>

          <cube-slide-item>
            <cube-scroll :data="data" :options="scrollOptions">
              <slot name="center">默认中间页</slot>
            </cube-scroll>
          </cube-slide-item>

          <cube-slide-item>
            <cube-scroll :data="data" :options="scrollOptions" >
              <slot name="right">默认右侧页</slot>
            </cube-scroll>
          </cube-slide-item> -->

<!-- 
          <cube-slide-item>
            <div style="height: 100%;">
              <slot name="right">默认右侧页</slot>
            </div>
          </cube-slide-item>
 -->
        </cube-slide>
      </div>
    </div>
  </div>

</template>

<script >

  import { findIndex } from 'common/js/util.js'

  export default {
    props: {
      data: Array,
      tabLabels: Array,
      defaultLabel: String,
    },
    data () {
      return {
        current: 0,
        selectedLabel: this.defaultLabel,
        disabled: false,
        loop: false,
        autoPlay: false,
        showDots: false,
        slideOptions: {
          listenScroll: true,
          probeType: 3,
          /* lock y-direction when scrolling horizontally and  vertically at the same time */
          directionLockThreshold: 0
        },
        scrollOptions: {
          click: false,
          /* lock x-direction when scrolling horizontally and  vertically at the same time */
          directionLockThreshold: 0,
        }
      }
    },
    methods: {
      changePage (current) {
        this.selectedLabel = this.tabLabels[current].label
        this.current = current
        console.log(current)
      },
      scroll (pos) {
        const x = Math.abs(pos.x)
        const tabItemWidth = this.$refs.tabBar.$el.clientWidth
        const slideScrollerWidth = this.$refs.slide.slide.scrollerWidth
        const deltaX = x / slideScrollerWidth * tabItemWidth
        this.$refs.tabBar.setSliderTransform(deltaX)
      }
    },
    computed: {
      initialIndex () {
        let index = 0
        index = findIndex(this.tabLabels, item => item.label === this.selectedLabel)
        return index
      }
    },
    components: {
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"

  /* 覆盖样式 */
  .cube-page
    background-color: white
    >.wrapper
      height: 100%
      overflow-x: hidden
      overflow-y: auto
  .tab-composite-view
    .cube-tab-bar
      height: 0.5rem
      margin-left: 0.40rem
      margin-right: 0.36rem
    .cube-tab
      color: #8E8E93
      font-size: 0.25rem
      padding: 0
    .cube-tab_active
      color: black
      font-size: 0.30rem
    .cube-tab-bar-slider
      background-color: #FFBA15
    .spec
      margin: 0.1rem 0.36rem 0 0.40rem
      border-bottom: 1px solid $spec-color
    .tab-slide-container
      position: absolute
      top: (0.5rem + 0.1rem + 0.02rem)
      left: 0
      right: 0
      bottom: 0
      .cube-scroll-content
          display: block
</style>


