<template>
  <div class="event">
    <div class="game_event_wrap" v-for="item in data" @click="goToEventDetail(item)">
      <div class="info">
        <div class="left">
          <img src="../assets/touxiang.png">
          <div class="user-info" style="margin-top: 0.17rem;margin-left: 0.24rem">
            <div>用户名称几个字</div>
            <div style="font-size: 0.20rem;margin-top: 0.08rem">2小时前</div>
          </div>
        </div>
        <label class="follow" @click.stop="changeFollowStatus(item)">已关注</label>
      </div>
      <label class="title">我是标题，请注意，我是关于这篇文章的标题</label>
      <div class="text content_style">正文部分以内容为主，游戏内容本身的介绍，对游戏的解说，玩游戏攻略，可以发表游戏BGM，游戏的推荐博文，游戏视频，游戏图片等。正文部分以内容为主，对游戏的解说，玩游戏攻略，可以发表游戏BGM，游戏的推荐博文，游戏视频，游戏图
        <div style="position: absolute;bottom: -0.022rem;right: 0;background-color: white;"><label>...</label><label style="color: #FFA535;">全文</label></div>
      </div>
      <div class="video">
        <img src="../assets/shipingbofanganniu.png">
      </div>
      <div class="bottom_comment_zan">
        <label>预计获取 20 IGC</label>
        <div class="total_comment_zan">
          <img class="zan_comment" src="../assets/huifu.png"><label> 999 </label>
          <img class="zan_comment" src="../assets/dianzan.png"><label> 999</label>
        </div>
      </div>
    </div>
  </div>
</template>


<script type="text/javascript">

  export default {
    props: {
      data: {
        type: Array,
        default: function() {
          return ['1']
        }
      }
    },
    data () {
      return {
        isFollow: true
      }
    },
    methods: {
      goToEventDetail(item) {
        this.$emit("goToEventDetail", item)
      },
      changeFollowStatus(item) {
        console.log("changeFollowStatus" + item)
      }
    },
    computed: {

    }
  }
</script>

<style scoped lang="stylus">

  .game_event_wrap
    display: flex
    flex-direction: column
    margin-top: 0.25rem
    padding: 0.17rem 0.21rem 0
    border: 1px solid rgb(225, 225, 225)
    border-radius: 3px
    box-sizing: content-box
    .info
      display: flex
      justify-content: space-between
      align-items: center
      .left
        display: flex
        img
          width: 0.72rem
          height: 0.72rem
      .follow
        width: 1.49rem
        height: 0.5rem
        font-size: 0.20rem
        text-align: center
        line-height: 0.5rem
        background: rgba(252,246,246,1)
        border-radius: 5px
    .title
      margin: 0.15rem 0
      color: #000000
    .text
      position: relative
      height: 1.08rem
      overflow: hidden;
      text-overflow: ellipsis;
      display:-webkit-box; //作为弹性伸缩盒子模型显示。
      -webkit-box-orient:vertical; //设置伸缩盒子的子元素排列方式--从上到下垂直排列
      -webkit-line-clamp: 3 //显示的行
    .video
      position: relative
      height: 2.84rem
      margin: 0.2rem 0
      background-color: #D8D8D8
      img
        position: absolute
        height: 0.94rem
        width: 0.94rem
        top: 50%
        left: 50%
        transform: translate(-50%, -50%)
    .bottom_comment_zan
      display: flex
      justify-content: space-between
      align-items: center
      height: 0.51rem
      font-size: 0.2rem
      border-top: 0.02rem solid rgb(225, 225, 225)
</style>


