<template>
  <div class="developer">

    <div class="info">
      <div class="left">
        <img src="../../assets/developer/touxaing.png">
        <div style="margin-left: 0.24rem">
          <div><label>网易游戏</label><label style="margin-left: 0.31rem">4.2分</label></div>
          <div style="font-size: 0.20rem;margin-top: 0.15rem">324万人关注</div>
        </div>
      </div>
      <label class="right" :class="{ right_active: isFollow }">{{isFollow ? '已关注' : '关注'}}</label>
    </div>

    <div class="tab_spec" style="height: 0.43rem"></div>

    <div class="dev_tab_composite">
      <tab-composite :data="mydata" :tabLabels="tabLabels" defaultLabel= "详情">

        <developer-evaluation slot="left" :data="mydata" :abstract="abstract"></developer-evaluation>

        <div class="dev_games" slot="center">
          <cube-scroll :data="mydata" :options="gameScrollOptions" @pulling-down="gamesPullingDown()" @pulling-up="gamesPullingUp()">
            <developer-games></developer-games>
          </cube-scroll>
        </div>
        
        <div class="dev_event" slot="right">
          <cube-scroll :data="mydata" :options="eventScrollOptions" @pulling-down="eventPullingDown()" @pulling-up="eventPullingUp()">
            <developer-event :data="mydata" @goToEventDetail="goToEventDetail"></developer-event>
          </cube-scroll>
        </div>      
      </tab-composite>
    </div>
    
  </div>
</template>

<script type="text/javascript">
  import TabComposite from 'pages/tab-composite.vue'
  import DeveloperGames from './developer-games.vue'
  import DeveloperEvaluation from 'pages/evaluation.vue'
  import DeveloperEvent from 'pages/event.vue'
  import SendMessageToApp from '../../jsAppInteractive'
  import { scrollOptions } from 'common/js/util.js'

  export default {
    data () {
      return {
        isFollow: true,
        tabLabels: [{
          label: '详情'
        }, {
          label: '游戏'
        }, {
          label: '动态'
        }],
        mydata: ['1', '2', '3'],
        abstract: '网易2001年正式成立在线游戏事业部，经过近20年的快速发展，网易的品牌价值已超过13亿美元，并跻身全球七大游戏公司之一。',
        eventScrollOptions: scrollOptions(),
        gameScrollOptions: scrollOptions()
      }
    },
    methods: {
      goToBack() {
        this.$router.back()
      },
      goToEventDetail(item) {
        SendMessageToApp('goToEventDetail')
        // this.$router.push({
        //   name: 'EventDetail'
        // })
      },
      gamesPullingDown() {
        setTimeout( () => {
          this.mydata = ['1', '2', '3']
        }, 1500)
      },
      gamesPullingUp() {
        setTimeout( () => {
          this.mydata = this.mydata.concat(['1','2'])
        }, 1500)
      },
      eventPullingDown() {
        setTimeout( () => {
          this.mydata = ['1', '2', '3']
        }, 1500)
      },
      eventPullingUp() {
        setTimeout( () => {
          this.mydata = this.mydata.concat(['1','2'])
        }, 1500)
      }
    },
    computed: {

    },
    components: {
      TabComposite,
      DeveloperEvaluation,
      DeveloperGames,
      DeveloperEvent,
    }
  }
</script>


<style scoped lang="stylus">
  @import "~common/stylus/variable"

  .developer
    background-color: #FFF
    color: $content-color
    font-size: 0.25rem
    .info
      display: flex
      justify-content: space-between
      align-items: center
      padding: 0.05rem 0.42rem 0.51rem 0.95rem
      background-color: #F2F2F2
      .left
        display: flex
        align-items: center
        img
          width: 0.72rem
          height: 0.72rem
      .right
        width: 1.49rem
        height: 0.5rem
        font-size: 0.20rem
        text-align: center
        line-height: 0.5rem
        background: rgba(252,246,246,1)
        border-radius: 5px
      .right_active
          color: white
          background-color: #FFA535
    .dev_tab_composite
      position: absolute
      top: (1.28rem + 0.43rem) //1.28->info, 0.43->tab_spec
      bottom: 0
      left: 0
      width: 100%
      .dev_event
        height: 100%
        box-sizing: border-box
        margin-left: 0.40rem
        margin-right: 0.36rem
        color: $content-color
        font-size: 0.25rem
      .dev_games
        height: 100%
    
  
</style>


