<template>
  <div class="developer_games">
    <div class="wrap" v-for="item in items">
      <img src="../../assets/developer/touxaing.png">
      <div class="info">
        <div class="left">
          <div>阴阳师</div>
          <div class="ver_info">
            <img class="icon" src="../../assets/android.png">
            <img class="icon" src="../../assets/ios.png">
          </div>
        </div>
        <div class="right">
          <div>5.0分</div>
          <div style="margin-top: 0.18rem;font-size: 0.2rem;">28万人推荐</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script >


  export default {
    props: {
      items: {
        type: Array,
        default: function () {
          return ['1','2','3','4','5','6','2','3','4','5','6','3','4','5','6']
        }
      }
    },
    data () {
      return {
        isFollow: true,
        tabLabels: [{
          label: '详情'
        }, {
          label: '游戏'
        }, {
          label: '动态'
        }],
        mydata: [],
        abstract: '网易2001年正式成立在线游戏事业部，经过近20年的快速发展，网易的品牌价值已超过13亿美元，并跻身全球七大游戏公司之一。'
      }
    },
    methods: {

    }
  }

</script>


<style scoped lang="stylus">
  @import "~common/stylus/variable"

  .developer_games
    background-color: #FFF
    color: $content-color
    font-size: 0.25rem
    margin-left: 0.40rem
    margin-right: 0.36rem
    .wrap
      display: flex
      align-items: center
      margin-bottom: 0.12rem
      padding: 0.09rem 0.22rem 0.09rem 0.10rem
      border: 1px solid $spec-color
      border-radius: 5px
      &:first-child
        margin-top: 0.21rem
      img
        width: 0.97rem
        height: 0.97rem
      .info
        display: flex
        justify-content: space-between
        width: (100% - 0.97rem)
        .ver_info
          display: flex
          align-items: center
          margin-top: 0.1rem
          .icon
            width: 0.29rem
            height: 0.36rem
          :last-child
            margin-left: 0.07rem
        .right
          text-align: right
    
  
</style>


